
.evaluate .type {
  padding: 18px 0px;
  margin: 0px 18px;
  font-size: 0px;
  position: relative;
}
.evaluate .type:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,17,0.2);
  content: '';
}
.evaluate .type > span {
  display: inline-block;
  margin-right: 8px;
  padding: 8px 12px;
  font-size: 12px;
  border-radius: 1px;
  color: #4d555d;
}
.evaluate .type > span > label {
  font-size: 8px;
  margin-left: 2px;
}
.evaluate .type > .positive {
  background-color: rgba(0,160,220,0.2);
}
.evaluate .type > .positive.active {
  color: #fff;
  background-color: #00a0dc;
}
.evaluate .type > .neagtive {
  background-color: rgba(77,85,93,0.2);
}
.evaluate .type > .neagtive.active {
  color: #fff;
  background-color: #4d555d;
}
.evaluate .switch {
  padding: 12px 18px;
  line-height: 24px;
  color: #93879f;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.evaluate .switch > i {
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
}
.evaluate .switch > i.on {
  color: #00c850;
}
.evaluate .switch > span {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}


