
.app .btn2 {
  position: fixed;
  bottom: 50px;
  right: 10px;
  z-index: 10;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  border: none;
  outline: none;
  color: #fff;
  font-size: 18px;
  background: #00f;
}
.app .menu {
  position: fixed;
  bottom: 50px;
  right: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  -webkit-transition: all 0.7s ease-in;
  transition: all 0.7s ease-in;
}
.app .menu.move-enter-active .inner {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition-timing-function: cubic-bezier(0, 0.57, 0.44, 1.97);
          transition-timing-function: cubic-bezier(0, 0.57, 0.44, 1.97);
}
.app .menu.move-enter-active .inner-1 {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}
.app .menu.move-enter-active .inner-2 {
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}
.app .menu.move-enter-active .inner-3 {
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
}
.app .menu.move-enter .inner,
.app .menu.move-leave-active .inner {
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}
.app .menu.move-enter .inner-1,
.app .menu.move-leave-active .inner-1 {
  -webkit-transform: translate3d(0, 60px, 0);
          transform: translate3d(0, 60px, 0);
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
}
.app .menu.move-enter .inner-2,
.app .menu.move-leave-active .inner-2 {
  -webkit-transform: translate3d(40px, 40px, 0);
          transform: translate3d(40px, 40px, 0);
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}
.app .menu.move-enter .inner-3,
.app .menu.move-leave-active .inner-3 {
  -webkit-transform: translate3d(60px, 0, 0);
          transform: translate3d(60px, 0, 0);
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}
.app .menu .inner {
  display: inline-block;
  position: absolute;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  background: #f00;
  text-align: center;
  color: #fff;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.app .menu .inner-1 {
  top: -50px;
  left: 10px;
}
.app .menu .inner-2 {
  left: -30px;
  top: -30px;
}
.app .menu .inner-3 {
  left: -50px;
  top: 10px;
}


