
.header {
  position: relative;
  color: #fff;
  background-color: #e6e2e1;
  overflow: hidden;
}
.header .background-box {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
  -webkit-filter: blur(4px);
          filter: blur(4px);
}
.header .content-box {
  position: relative;
  padding: 16px 12px 18px 14px;
  font-size: 0px;
}
.header .content-box .photo {
  display: inline-block;
  vertical-align: top;
}
.header .content-box .photo > img {
  border-radius: 2px;
}
.header .content-box .content {
  display: inline-block;
  margin-left: 16px;
}
.header .content-box .content .title {
  margin: 2px 0px 8px 0px;
}
.header .content-box .content .title .brand {
  width: 30px;
  height: 18px;
  display: inline-block;
  vertical-align: top;
  background-image: url("img/brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-box .content .title .brand {
    background-image: url("img/brand@3x.png");
}
}
.header .content-box .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}
.header .content-box .content .time {
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 12px;
}
.header .content-box .content .supports .type {
  display: inline-block;
  width: 12px;
  height: 12px;
  vertical-align: top;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.header .content-box .content .supports .type.decrease {
  background-image: url("img/decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-box .content .supports .type.decrease {
    background-image: url("img/decrease_1@3x.png");
}
}
.header .content-box .content .supports .type.discount {
  background-image: url("img/discount_1@2x.png");
}
