.header .content-box .content .supports .type.discount {
    background-image: url("img/discount_1@3x.png");
}
}
.header .content-box .content .supports .type.special {
  background-image: url("img/special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-box .content .supports .type.special {
    background-image: url("img/special_1@3x.png");
}
}
.header .content-box .content .supports .type.invoice {
