}
}
.header .content-box .content .supports .type.guarantee {
  background-image: url("img/guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-box .content .supports .type.guarantee {
    background-image: url("img/guarantee_1@3x.png");
}
}
.header .content-box .content .supports .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-box .supports-btn {
  position: absolute;
  right: 14px;
  bottom: 18px;
  height: 24px;
  line-height: 24px;
  padding: 0px 8px;
  text-align: center;
  border-radius: 14px;
  background-color: rgba(0,0,0,0.2);
}
.header .content-box .supports-btn > span {
  display: inline-block;
  font-size: 10px;
}
.header .content-box .supports-btn > i {
  display: inline-block;
  width: 10px;
  height: 10px;
  vertical-align: center;
  margin-left: 2px;
  font-size: 10px;
}
.header .content-box .supports-btn > i .icon-keyboard_arrow_right {
  font-size: 10px;
  line-height: 24px;
}
.header .bulletin-box {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0px 22px 0px 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: rgba(7,17,27,0.2);
}
.header .bulletin-box .bulletin-title {
  display: inline-block;
  margin-top: 9px;
  width: 22px;
  height: 12px;
  vertical-align: top;
  background-size: 22px 12px;
  background-image: url("img/bulletin@2x.png");
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-box .bulletin-title {
    background-image: url("img/bulletin@3x.png");
}
}
.header .bulletin-box .bulletin-text {
  font-size: 10px;
  vertical-align: top;
  margin: 0px 4px 0px 0px;
}
.header .bulletin-box .icon-keyboard_arrow_right {
  position: absolute;
  top: 8px;
  right: 12px;
  font-size: 10px;
}
.header .pop-detail {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(7,17,27,0.8);
}
.header .pop-detail.bounce-enter-active {
  -webkit-animation: bounce-inA 1s;
          animation: bounce-inA 1s;
}
.header .pop-detail.bounce-leave-active {
  -webkit-animation: bounce-outB 1s;
          animation: bounce-outB 1s;
}
.header .pop-detail .detail-box {
  width: 100%;
  min-height: 100%;
}
.header .pop-detail .detail-box .detail-main {
  padding: 36px 36px 64px 36px;
}
.header .pop-detail .detail-box .detail-main > h1 {
  width: 100%;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  text-align: center;
}
.header .pop-detail .detail-box .detail-main .star-box {
  margin-top: 18px;
  padding: 2px 0px;
  text-align: center;
}
.header .pop-detail .detail-box .detail-main .supports > li {
  font-size: 12px;
  line-height: 16px;
  font-weight: 200;
}
.header .pop-detail .detail-box .detail-main .supports > li > i {
  display: inline-block;
  margin: 0px 6px 12px 12px;
  width: 16px;
  height: 16px;
  vertical-align: top;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.header .pop-detail .detail-box .detail-main .supports > li > i.decrease {
  background-image: url("img/decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
