}
}
.header .pop-detail .detail-box .detail-main .supports > li > i.special {
  background-image: url("img/special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .pop-detail .detail-box .detail-main .supports > li > i.special {
    background-image: url("img/special_2@3x.png");
}
}
.header .pop-detail .detail-box .detail-main .supports > li > i.invoice {
  background-image: url("img/invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
