    background-image: url("img/invoice_2@3x.png");
}
}
.header .pop-detail .detail-box .detail-main .supports > li > i.guarantee {
  background-image: url("img/guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .pop-detail .detail-box .detail-main .supports > li > i.guarantee {
    background-image: url("img/guarantee_2@3x.png");
}
}
.header .pop-detail .detail-box .detail-main .bulletin {
  font-size: 12px;
  line-height: 24px;
  font-weight: 200;
  text-align: justify;
}
.header .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0px auto;
  font-size: 32px;
  clear: both;
}
