
.goods-info-page {
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 49px;
  width: 100%;
  background: #fff;
}
.goods-info-page.goods-info-move-enter-active {
  -webkit-animation: bounce-inA 1s;
          animation: bounce-inA 1s;
}
.goods-info-page.goods-info-move-leave-active {
  -webkit-animation: bounce-outB 1s;
          animation: bounce-outB 1s;
}
.goods-info-page .info-header {
  position: relative;
  width: 100%;
  height: 0px;
  padding-top: 100%; /*这种写法：保持始终是一个正方形*/
}
.goods-info-page .info-header > img {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.goods-info-page .info-header > i {
  position: absolute;
  top: 12px;
  left: 12px;
  display: block;
  padding: 6px;
  color: #dcdcdc;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 50%;
  -webkit-box-shadow: 0px 0px 4px 0px #dcdcdc;
          box-shadow: 0px 0px 4px 0px #dcdcdc;
}
.goods-info-page .info-title {
  padding: 18px;
  position: relative;
}
.goods-info-page .info-title > h1 {
  font-size: 14px;
  line-height: 28px;
  font-weight: 700;
  color: #07111b;
}
.goods-info-page .info-title > p {
  font-size: 10px;
  line-height: 20px;
  color: #93999f;
}
.goods-info-page .info-title > p > span {
  margin-right: 12px;
}
.goods-info-page .info-title .price {
  line-height: 24px;
  font-weight: 800;
  padding-top: 18px;
}
.goods-info-page .info-title .price .now-price {
  margin-right: 8px;
  line-height: 16px;
  font-size: 16px;
  color: #f01414;
}
.goods-info-page .info-title .price .old-price {
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.goods-info-page .info-title .buyctrl-box {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.goods-info-page .info-title .buy-btn {
  position: absolute;
  right: 18px;
  bottom: 18px;
  height: 24px;
  line-height: 24px;
  padding: 0px 12px;
  font-size: 12px;
  color: #fff;
  z-index: 10;
  border-radius: 12px;
  background-color: #00a0dc;
}
.goods-info-page .info-title .buy-btn.buy-move-enter-active,
.goods-info-page .info-title .buy-btn.buy-move-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.goods-info-page .info-title .buy-btn.buy-move-enter,
.goods-info-page .info-title .buy-btn.buy-move-leave-to {
  opacity: 0;
}
.goods-info-page .info-text {
  padding: 18px;
  position: relative;
}
.goods-info-page .info-text > h1 {
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 28px;
  font-weight: 500;
  color: #07111b;
}
.goods-info-page .info-text > p {
  padding: 0px 8px;
  font-size: 12px;
  text-align: justify;
  line-height: 24px;
  color: #4d555d;
}
.goods-info-page .info-evaluate {
  padding-top: 18px;
}
.goods-info-page .info-evaluate > h1 {
  margin-left: 18px;
  font-size: 14px;
  line-height: 28px;
  font-weight: 500;
  color: #07111b;
}
.goods-info-page .info-rating {
  padding: 18px;
  padding-top: 0px;
}
.goods-info-page .info-rating > ul > li {
  position: relative;
  padding: 16px 0px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.goods-info-page .info-rating .user {
  position: absolute;
  top: 16px;
  right: 0px;
  line-height: 12px;
  font-size: 0px;
}
.goods-info-page .info-rating .user > span {
  display: inline-block;
  margin-right: 6px;
  padding-top: 4px;
  vertical-align: top;
  font-size: 12px;
  color: #93999f;
}
.goods-info-page .info-rating .user > img {
  border-radius: 50%;
}
.goods-info-page .info-rating .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 12px;
  color: #93999f;
}
.goods-info-page .info-rating .text {
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}
.goods-info-page .info-rating .text > i {
  position: relative;
  top: 2px;
  margin-right: 4px;
  line-height: 24px;
  font-size: 16px;
}
.goods-info-page .info-rating .text > i.icon-thumb_up {
  color: #00c850;
}
.goods-info-page .info-rating .text > i.icon-thumb_down {
  color: #f01414;
  top: 4px;
}
.goods-info-page .info-rating .no-ratings {
  padding: 16px 0px;
  font-size: 12px;
  line-height: 16px;
  color: #93879f;
}
@-webkit-keyframes bounce-inA {
0% {
    -webkit-transform: translate3d(460px, 0px, 0px);
            transform: translate3d(460px, 0px, 0px);
}
50% {
    -webkit-transform: translate3d(-100px, 0px, 0px);
            transform: translate3d(-100px, 0px, 0px);
}
100% {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
}
}
@keyframes bounce-inA {
0% {
    -webkit-transform: translate3d(460px, 0px, 0px);
            transform: translate3d(460px, 0px, 0px);
}
50% {
    -webkit-transform: translate3d(-100px, 0px, 0px);
            transform: translate3d(-100px, 0px, 0px);
}
100% {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
}
}
@-webkit-keyframes bounce-outB {
0% {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
}
100% {
    -webkit-transform: translate3d(460px, 0px, 0px);
            transform: translate3d(460px, 0px, 0px);
}
}
@keyframes bounce-outB {
0% {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
}
100% {
    -webkit-transform: translate3d(460px, 0px, 0px);
            transform: translate3d(460px, 0px, 0px);
}
}


