
.title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 30px auto;
}
.title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  position: relative;
}
.title .line:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(255,255,255,0.5);
  content: '';
}
.title .text {
  padding: 0px 12px;
  font-size: 15px;
  font-weight: 700;
}


