
.app .nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid rgba(7,17,17,0.2); /*border-1px(rgba(7, 17, 17, 0.1))*/
}
.app .nav .nav-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  color: #4f5559;
}
.app .nav .nav-item > a {
  display: block;
  font-size: 14px;
}
.app .nav .nav-item > a.router-link-active {
  background-color: #22affe;
  color: #fff;
  font-weight: bold;
}


