
.buyctrl {
  font-size: 0px;
}
.buyctrl .less-btn,
.buyctrl .plus-btn {
  display: inline-block;
  padding: 6px;
  color: #22affe;
  font-size: 24px;
  line-height: 24px;
}
.buyctrl .less-btn.movetoleft-enter-active,
.buyctrl .plus-btn.movetoleft-enter-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.buyctrl .less-btn.movetoleft-leave-active,
.buyctrl .plus-btn.movetoleft-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.buyctrl .less-btn.movetoleft-enter,
.buyctrl .plus-btn.movetoleft-enter,
.buyctrl .less-btn.movetoleft-leave-active,
.buyctrl .plus-btn.movetoleft-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0) rotate(270deg);
          transform: translate3d(24px, 0, 0) rotate(270deg);
}
.buyctrl .cont-num {
  display: inline-block;
  padding-top: 6px;
  font-size: 12px;
  line-height: 24px;
  color: #93999f;
  vertical-align: top;
  text-align: center;
}


